.header {
    background-color: aliceblue;
    padding: 20px;
    margin: 0;
}
.headerList {
    display: flex;
    list-style: none;
    margin: 0 auto;
    padding: 10px;
    gap: 30px;
    justify-content: center;
}
.headerItem {
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-weight: 600;
    width: 100px;
    text-align: center;
    background-color: blue;
    padding: 15px;
    border-radius: 10px;
}